import React from "react";

const Jumbotron = ({children}) =>
    <div style={{height: 300, clear: "both"}} className="jumbotron text-center">
        <h1>News-4-Choo</h1>
        <h3>Search for and save articles of interest!</h3>
    </div>;

export default Jumbotron;


